<template>
  <div>
    <manager-header>
      <template v-slot:search>
        <el-input placeholder="" style="width: 200px" v-model="name"></el-input>
        <el-button type="info" plain style="margin-left: 10px" @click="load(1)"></el-button>
      </template>
      <template v-slot:operation>
        <el-button type="primary" plain @click="handleAdd"></el-button>
        <el-button type="danger" plain @click="delBatch"></el-button>
      </template>
    </manager-header>

    <div class="table">
      <el-table :data="tableData" stripe  @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center"></el-table-column>
        <el-table-column prop="id" label="" width="80" align="center" sortable></el-table-column>
        <el-table-column label="">
          <template v-slot="scope">
            <div style="display: flex; align-items: center">
              <el-image style="width: 40px; height: 40px; " v-if="scope.row.img"
                        :src="scope.row.img" :preview-src-list="[scope.row.img]"></el-image>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" show-overflow-tooltip></el-table-column>
        
        <el-table-column prop="price" label="" show-overflow-tooltip></el-table-column>
        
        <el-table-column prop="typeName" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="businessName" label="" show-overflow-tooltip></el-table-column>
        <el-table-column label="" width="180" align="center">
          <template v-slot="scope">
            <el-button plain type="primary" @click="handleEdit(scope.row)" size="mini"></el-button>
            <el-button plain type="danger" size="mini" @click="del(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <el-pagination
            background
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-sizes="[5, 10, 20]"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>
    </div>


    <el-dialog title="" :visible.sync="fromVisible" width="40%" :close-on-click-modal="false" destroy-on-close @close="cancel">
      <el-form label-width="100px" style="padding-right: 50px" :model="form" :rules="rules" ref="formRef">
        <el-form-item label="">
          <el-upload
              class="avatar-uploader"
              :action="$baseUrl + '/files/upload'"
              :headers="{ token: user.token }"
              list-type="picture"
              :on-success="handleAvatarSuccess"
          >
            <el-button type="primary"></el-button>
          </el-upload>
        </el-form-item>
        <el-form-item prop="name" label="">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item prop="name" label="">
          <el-input v-model="form.price" autocomplete="off"></el-input>
        </el-form-item>
<!--
        <el-form-item prop="typeId" label="">
          <el-select v-model="form.typeId" placeholder="" style="width: 100%">
            <el-option v-for="item in typeData" :label="item.name" :value="item.id" :key="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="unit" label="">
          <el-input v-model="form.unit" autocomplete="off"></el-input>
        </el-form-item>
      -->
        <el-form-item prop="description" label="">
          <div id="editor" style="width: 100%"></div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="save"> </el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="editorVisible" width="50%">
      <div v-html="this.viewData" class="w-e-text"></div>
    </el-dialog>
  </div>
</template>

<script>
import E from 'wangeditor'
import ManagerHeader from '@/components/manager/ManagerHeader.vue'

let editor
function initWangEditor(content) {	setTimeout(() => {
  if (!editor) {
    editor = new E('#editor')
    editor.config.placeholder = ''
    editor.config.uploadFileName = 'file'
    editor.config.uploadImgServer = 'http://localhost:9090/files/wang/upload'
    editor.create()
  }
  editor.txt.html(content)
}, 0)
}

export default {
  name: "Notice",
  components: { ManagerHeader },
  data() {
    return {
      tableData: [],  // 
      pageNum: 1,   // 
      pageSize: 10,  // 
      total: 0,
      name: null,
      fromVisible: false,
      editorVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('xm-user') || '{}'),
      rules: {
        title: [
          {required: true, name: '', trigger: 'blur'},
        ],
        content: [
          {required: true, img: '', trigger: 'blur'},
        ]
      },
      ids: [],
      typeData: [],
      viewData: null
    }
  },
  created() {
    this.load(1)
    this.loadType()
  },
  methods: {
    loadType() {
      this.$request.get('/type/selectAll').then(res => {
        if (res.code === '200') {
          this.typeData = res.data
        } else {
          this.$message.error(res.msg)
        }
      })
    },
    handleAdd() {   // 
      if ('' !== this.user.status) {
        this.user.status = ''
        //this.$message.warning('')
        //return
      }
      this.form = {}  // 
      initWangEditor('')
      this.fromVisible = true   // 
    },
    handleEdit(row) {   // 
      this.form = JSON.parse(JSON.stringify(row))  // form  
      initWangEditor(this.form.description || '')
      this.fromVisible = true   // 
    },
    viewEditor(content) {
      this.viewData = content
      this.editorVisible = true
    },
    cancel() {
      this.fromVisible = false
      location.href = '/goods'
    },
    save() {   //   
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.form.description = editor.txt.html()
          this.$request({
            url: this.form.id ? '/goods/update' : '/goods/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '200') {  // 
              this.$message.success('')
              // this.load(1)
              this.fromVisible = false
              location.href = '/goods'
            } else {
              this.$message.error(res.msg)  // 
            }
          })
        }
      })
    },
    del(id) {   // 
      this.$confirm('', '', {type: "warning"}).then(response => {
        this.$request.delete('/goods/delete/' + id).then(res => {
          if (res.code === '200') {   // 
            this.$message.success('')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 
          }
        })
      }).catch(() => {
      })
    },
    handleSelectionChange(rows) {   // 
      this.ids = rows.map(v => v.id)   //  [1,2]
    },
    delBatch() {   // 
      if (!this.ids.length) {
        this.$message.warning('')
        return
      }
      this.$confirm('', '', {type: "warning"}).then(response => {
        this.$request.delete('/goods/delete/batch', {data: this.ids}).then(res => {
          if (res.code === '200') {   // 
            this.$message.success('')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 
          }
        })
      }).catch(() => {
      })
    },
    load(pageNum) {  //  / 
      if (pageNum) this.pageNum = pageNum
      const key = (this.name || '').toString().trim()
      if (key) {
        // 
        this.$request.get('/goods/selectByName?name=' + encodeURIComponent(key)).then(res => {
          if (res.code === '200') {
            this.tableData = res.data || []
            //  total 
            this.total = this.tableData.length
          } else {
            this.$message.error(res.msg)
          }
        })
      } else {
        // 
        this.$request.get('/goods/selectPage', {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            name: this.name,
          }
        }).then(res => {
          this.tableData = res.data?.list
          this.total = res.data?.total
        })
      }
    },
    
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
    handleAvatarSuccess(response, file, fileList) {
      this.form.img = response.data
    },
  }
}
</script>

<style scoped>

</style>
